/**
 * FormLanding.js
 * Wrapper that takes in the main landing form and
 * renders the 3 images with description, along
 * with the promo button
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';

import Logos from 'assets/images/InsurerLogos';
import ReactSlickCarousel from 'components/Templates/ReactSlickCarousel/ReactSlickCarousel';

import './FormLandingSplash.scss';

class FormLandingSplash extends Component {
  renderLogo() {
    const { colorLogos } = this.props;
    const logosToUse = colorLogos ? Logos.small : Logos.smallTinted;

    return _.map(logosToUse, (logo, index) => (
      <div className="insurer-logo-sq" key={index}>
        <img src={logo} alt={`logo-${index}`} />
      </div>
    ));
  }

  render() {
    const { desc, img } = this.props;
    return (
      <Row className="form-landing-splash-container form-landing-insurer-carousel" noGutters>
        <Col xs={12}>
          <img className="splash-img" src={img} alt="splash-img" />
          {desc}
          <div className="mas-container">
            {/* <img src={Mas} alt="Mas.svg" />
            <p className="h7">Licensed Insurance Broker & Exempted Financial Advisor</p> */}
          </div>
        </Col>
        <Col xs={{ size: 10, offset: 1 }} className="insurer-logo-carousel">
          <div className="carousel-container">
            <ReactSlickCarousel
              lazyLoad
              autoplay
              slidesToShow={5}
              arrows
              className="white-sliders"
            >
              {this.renderLogo()}
            </ReactSlickCarousel>
          </div>
          <p className="h7">Over 20 trusted insurance partners and 50,000 registered users got their insurance solutions via PolicyPal</p>
        </Col>
      </Row>
    );
  }
}

FormLandingSplash.defaultProps = {
  colorLogos: false,
};

FormLandingSplash.propTypes = {
  desc: PropTypes.object.isRequired,
  img: PropTypes.string.isRequired,
  colorLogos: PropTypes.bool,
};

export default FormLandingSplash;
